:root {
    --primary: #59cefd;
    --primarylight: #B9F2F3;
    --white: #ffffff;
    --secondary: #6E5EFE;
    --black: #474747;
}

/* reset some of the basic styles */
* {
    box-sizing: border-box;
}

p {
    margin: 0;
}
/* end reset */

html {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background-color: #f6f4f0;
    margin: 0;
    padding: 0;
}

a {
    color: #59cefd;
    color: var(--primary);
    text-decoration: none;
    text-transform: lowercase;
    font-family: 'Inter', sans-serif;
    font-weight: normal;
}

a:hover {
    color: #6E5EFE;
    color: var(--secondary);
}

h1 {
    font-family: 'Montserrat', sans-serif;
    color: #474747;
    color: var(--black);
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 0;
}

@media (max-width: 400px) {
    h1 {
        font-size: 26px;
    }
}

h5 {
    line-height: 0;
    font-weight: lighter;
    font-family: 'Source Sans Pro', sans-serif;
    text-transform: uppercase;
    font-size: 22px;
    color: #59cefd;
    color: var(--primary);
    margin-top: 20px;
}


.portfolio-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.main-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 6 1;
            flex: 6 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}


.projects-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.projects-display {
    margin-top: 0;
}

.complete-project {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /*box-shadow: 0 0 5px 2px #d6d6d6;*/
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 30px;
    margin-left: 30px;
    /*background: var(--white);*/
    /*border-radius: 7px;*/
}

.project-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: -30px;
    margin-bottom: 20px;
    max-width: 300px;
    min-width: 300px;
    max-height: 520px;
    min-height: 520px;
}

.project-card a {
    color: darkslateblue;
    border-bottom: 2px solid var(--primarylight);
    padding: 1px;
    font-size: 13px;
    font-weight: bold;
}

.project-card a:hover {
    color: var(--primary);
}

