/*    display: flex;*/
/*    flex-direction: column;*/
/*    justify-content: space-between;*/
/*    max-width: 300px;*/
/*    min-width: 300px;*/
/*    min-height: 350px;*/
/*    max-height: 350px;*/
/*}*/

.project-name {
    text-align: left;
}

.project-links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: left;
    margin-top: 10px;
    margin-right: 30px;
}

.project-img img {
    width: 100%;
    height: auto;
    box-shadow: 2px 3px 5px #ababab;
    border-radius: 4px;
    margin-bottom: 1.5rem;
}

.gradient-div {
    /*background: rgb(140,220,255);*/
    /*background: linear-gradient(270deg, rgba(140,220,255,1) 12%, rgba(80,201,255,1) 30%, rgb(110, 94, 254) 74%);*/
    background: rgb(91,227,207);
    background: linear-gradient(270deg, rgb(185, 242, 243) 21%, rgb(140, 220, 255) 51%, rgb(140, 72, 217) 96%);
    height: .3rem;
    width: 70%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.project-description {
    max-height: 100px;
    min-height: 100px;
}

.about {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 780px;
}

.about p {
    font-family: 'Inter', sans-serif;
    font-size: 17px;
    color: var(--black);
    font-weight: bolder;
    line-height: 30px;
}

.about-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-top: 5px;
    margin-right: 16px;
    margin-bottom: 16px;
}

.about-right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 3 1;
            flex: 3 1;
    padding-left: 10px;
}

.about-pic {
    width: 250px;
    /*box-shadow: 0 0 5px 2px #d6d6d6;*/
}

@media (max-width: 855px) {
    .about p {
        font-size: 15px;
    }
}

@media (max-width: 797px) {
    .about p {
        font-size: 14px;
    }
}

@media (max-width: 778px) {
    .about-right {
        min-width: 300px;
        max-width: 400px;
    }
    .about p {
        font-size: 14px;
        line-height: 28px;
    }
}

@media (max-width: 740px) {
    .about p {
        font-size: 14px;
        line-height: 25px;
    }
}

@media (max-width: 714px) {
    .about p {
        font-size: 14px;
        line-height: 23px;
    }
}

@media (max-width: 700px) {
    .about {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
    .about-right {
        min-width: 300px;
        max-width: 400px;
    }
    .about p {
        font-size: 14px;
        line-height: 25px;
    }
}

@media (max-width: 500px) {
    .about {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .about-right {
        min-width: 200px;
        max-width: 300px;
    }
    .about p {
        font-size: 14px;
        line-height: 25px;
    }
}
.navbar {
    background: #fff;
    font-size: 18px;
    display: -webkit-flex;
    display: flex;
    box-shadow: 0 0 5px 2px #d6d6d6;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    overflow: hidden;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
    min-height: 75px;
    max-height: 75px;
    font-family: 'Inter', sans-serif;
}

nav a {
    color: var(--primary);
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    text-transform: lowercase;
    font-weight: bold;
    padding: 30px;
}

.nav-links a:hover {
    background: #f8f8f8;
    color: var(--secondary);
    cursor: pointer;
}

.nav-links > .active {
    color: var(--secondary);
}

.social a {
    background: #fff;
    color: var(--black);
    font-size: 30px;
    padding: 5px;
    margin-right: 5px;
}

.social a:hover {
    background: #fff;
    color: var(--secondary);
    font-size: 30px;
    padding: 5px;
}

.logo {
    width: 65px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-family: 'Montserrat', sans-serif;
    font-size: 30px;
    font-weight: bolder;
    color: var(--black);
    padding: 7px;
    text-transform: uppercase;
    margin-bottom: 15px;
}

.logo:hover {
    color: var(--black) !important;
}
.footer-container {
    background-color: #353535;
    overflow: hidden;
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    margin-top: 50px;
}

.footer-content {
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.footer-content img{
    width: 5%;
    margin-top: 10px;
    margin-bottom: 20px;
}
